.kiaan-panel-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  background-color: rgba(0, 0, 0, 0.3);
}

.kiaan-panel-container.active {
  opacity: 1;
  pointer-events: all;
}

.kiaan-panel {
  background: rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 90%;
  max-width: 480px;
  height: 80%;
  max-height: 700px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transform: scale(0.9);
  transition: transform 0.3s ease;
}

.kiaan-panel-container.active .kiaan-panel {
  transform: scale(1);
}

.kiaan-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.kiaan-panel-title {
  color: white;
  font-size: 18px;
  font-weight: 600;
}

.kiaan-panel-close {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  line-height: 1;
}

.kiaan-panel-tabs {
  display: flex;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.kiaan-panel-tab {
  flex: 1;
  padding: 12px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: background-color 0.2s;
}

.kiaan-panel-tab.active {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  font-weight: 500;
}

.kiaan-panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
}

.kiaan-panel-footer {
  padding: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* Modes */
.kiaan-mode {
  display: none;
  height: 100%;
}

.kiaan-mode.active {
  display: flex;
  flex-direction: column;
}
