.kiaan-voice-orb {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background: linear-gradient(135deg, #4f7cff 0%, #9d4edd 100%);
  box-shadow: 0 0 15px rgba(79, 124, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 9998;
  user-select: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  animation: pulse 3s infinite;
  touch-action: none;
}

.kiaan-voice-orb:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(79, 124, 255, 0.7);
}

.kiaan-voice-orb.dragging {
  animation: none;
  transition: none;
}

.kiaan-voice-orb svg {
  width: 32px;
  height: 32px;
  pointer-events: none;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 15px rgba(79, 124, 255, 0.5);
  }
  50% {
    box-shadow: 0 0 25px rgba(157, 78, 221, 0.7);
  }
  100% {
    box-shadow: 0 0 15px rgba(79, 124, 255, 0.5);
  }
}
